import styles from './Transaction.module.css';


const TransactionHistory = ({items}) => {
    return (
        <>
        <table className ={styles.transaction_history}>
          <thead className= {styles.head}>
            <tr>
              <th>Type</th>
              <th>Amount</th>
              <th>Currency</th>
            </tr>
          </thead>
        
          <tbody className ={styles.body}>
              {items.map(el => {
                  const {id, type, amount, currency} = el;
              return(
                    <tr key ={id}>
                    <td>{type}</td>
                    <td>{amount}</td>
                    <td>{currency}</td>
                  </tr>
              );
              })}
                
          </tbody>
        </table>
        </>
            );
};


